




































/*选择网络*/
.choose_net {
  position: absolute;
  width: 400px;
  height: 220px;
  z-index: 100;
  box-shadow: none;
  top: 50%;
  left: 50%;
  padding: 0;
  margin: -110px 0 0 -200px;
  border-radius: 5px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}

.choose_net .jscroll {
  width: 100%;
  margin: 0;
}

.choose_net p {
  border-bottom: 1px solid #aaa;
  line-height: 30px;
}

.choose_net li {
  margin: 10px 20px;
}

.choose_net li a {
  display: block;
  height: 40px;
  line-height: 40px;
  color: #666;
  font-size: 12px;
  border-radius: 5px;
  background: url("../assets/img/l_arrow.png") no-repeat -6px center;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.choose_net li a:hover {
  background-color: #edf2fb;
  color: #969bad;
  padding-left: 20px;
  background-position: 6px center;
}

.choose_net .title {
  width: 100%;
  height: 49px;
  line-height: 49px;
  border-bottom: 1px solid #e3e4eb;
  border-radius: 5px 5px 0 0;
  text-indent: 20px;
}



