@charset "utf-8";
/* CSS Document */
/*reset*/
html {
  height: 100%;
}

body {
  position: relative;
  background: #f5f5f5;
  color: #2b2b2b;
  overflow: hidden;
  height: 100%;
}

body, div, td, font, em {
  font-size: 12px;
  line-height: 1.2em;
  font-family: "Microsoft YaHei", Tahoma, Arial, "宋体", "黑体";
}

body, div, ul, ol, li, h1, h2, h3, h4, h5, h6, form, fieldset, legend, input, textarea, p, th, td, dl, dt, dd {
  margin: 0;
  padding: 0;
}

table {
  border-spacing: 0;
  border-collapse: collapse
}

img {
  border: none;
}

address, caption, cite, code, dfn, em, th, var, b, font, i {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style-image: none;
  list-style-position: outside;
  list-style-type: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  zoom: 1;
}

input, textarea, select, button {
  font-size: 12px;
  font-weight: inherit;
  vertical-align: middle;
}

input[type=radio], input[type=checkbox] {
  vertical-align: -2px;
}

textarea {
  resize: none;
  overflow-x: hidden;
  overflow-y: auto;
  outline: medium none;
  word-wrap: break-word;
}

a {
  text-decoration: none;
  color: #6ab0f5;
}

a:focus {
  outline-style: none;
  outline-width: 0;
  outline: none;
}

/*a{_blr:expression(this.onFocus=this.blur());}*/
a:link, a:visited, a:active {
  text-decoration: none;
}

a:hover, .news-list li a:hover {
