/*input*/
.has-general, .select_box {
  width: 78%;
  height: 30px;
  line-height: 30px;
  position: relative;
  vertical-align: middle;
}

.has-general input, .has-general.textarea textarea {
  width: 100%;
  border: 1px solid transparent;
  outline: 0;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  border-radius: 3px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  text-indent: 2px;
}

.has-general input, .has-general.textarea textarea {
  border-color: #eee;
}

.apply_tab .has-general input, .apply_tab .has-general.textarea textarea {
  border-color: transparent;
}

.has-general:hover input, .has-general.textarea:hover textarea, .process_edit .has-general input {
  border-color: #5597b8;
  box-shadow: 0 1px 5px rgba(85, 151, 184, .4);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.has-error input, .has-error:hover input, .has-error.textarea textarea, .has-error.textarea:hover textarea, .apply_tab .has-error input, .apply_tab .has-error:hover input, .apply_tab .has-error.textarea textarea, .apply_tab .has-error.textarea:hover textarea {
  color: #ea583d;
  border-color: #ea583d;
  box-shadow: none;
  background: #ffece8;
}

.has-success input, .has-success:hover input, .apply_tab .has-success input, .apply_tab .has-success:hover input {
  color: #98c847;
  border-color: #98c847;
  box-shadow: none;
}

.control-label {
  height: 30px;
  line-height: 30px;
  color: #fff;
  position: absolute;
  background: #ea583d;
  border-radius: 3px;
  padding: 0 10px;
  top: -35px;
  right: 0;
  display: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.has-error .control-label {
  display: block;
  top: -40px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.has-general em.tips {
  min-width: 5px;
  padding: 0 2px;
  background: #fff;
  position: absolute;
  right: 0;
  top: 1px;
  bottom: 1px;
  line-height: 30px;
  color: #888;
}

.select_box {
  height: 28px;
  line-height: 28px;
  border: 1px solid #ddd;
  border-radius: 3px;
  vertical-align: middle;
  position: relative;
  text-indent: 5px;
}

.select_box .dropdown_menu {
  display: block;
  opacity: 1;
}

.select_box.current .dropdown_menu {
  display: block;
  opacity: 1;
}

.result_main .select_box {
  background: #fafafa;
}

.has-general.large, .has-general.large input, .select_box.large {
  height: 38px;
  line-height: 38px;
}

.btn_items .dropdown_menu {
  display: none;
  opacity: 0;
}

.btn_items .dropdown_menu li a {
  height: 30px;
  line-height: 30px;
}

.btn_items .dropdown_menu li:hover {
  box-shadow: none !important;
}

.apply_tab .select_box {
