/*登录，选择网络*/
.autoOut {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
}

.autoImg {
  position: relative;
  overflow: hidden;
  z-index: -1;
}

.loading {
  position: absolute;
  width: 160px;
  height: 100px;
  top: 50%;
  left: 50%;
  padding: 20px;
  margin: -70px 0 0 -100px;
  z-index: 202;
  color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
  border-radius: 3px;
  overflow: hidden;
}

.loading img {
  width: 550px;
  vertical-align: bottom;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -170px 0 0 -275px;
}

/*pop*/
.pop {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 99;
  border-radius: 4px;
  box-shadow: 0 1px 50px rgba(106, 121, 133, .4);
  width: 430px;
  height: 320px;
  margin: -160px 0 0 -215px;
}

.pop .title {
  height: 39px;
  line-height: 39px;
  border-bottom: 1px solid #e3e4eb;
  position: relative;
}

.bottom_btn {
  width: 100%;
  height: 49px;
  line-height: 49px;
  position: absolute;
  left: 0;
  bottom: 10px;
}

.popInner {
  position: relative;
}

.class_list li {
  float: left;
  width: 20px;
  height: 20px;
  border: 1px solid #fff;
  padding: 4px;
  background: #fff;
  border-radius: 50%;
  cursor: pointer;
  margin: 5px 9px 5px 0;
}

.class_list li .apply_logo {
  width: 20px;
  height: 20px;
}

.class_list li.current, .class_list li:hover {
  background: #f6f6f6;
  border-color: #eee;
}

.pop .title_ser {
  height: 49px;
}

.pop .title_ser .search_bar, .pop .title_ser .search_bar input {
  height: 49px;
  line-height: 49px;
}

.approval {
  width: 97%;
  height: 90px;
  border: 1px solid #e3e4eb;
}

.switch {
  display: inline-block;
  width: 50px;
  height: 22px;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 40px;
  background: #6ab0f5;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  position: relative;
}

.switch em {
  display: inline-block;
  width: 22px;
  height: 22px;
  line-height: 0;
  font-size: 0;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  box-shadow: -1px 1px 3px #747474;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  left: 0;
  z-index: 2;
}

.switch .before {
  display: inline-block;
  width: 48px;
  height: 20px;
  border: 1px solid #eee;
  border-radius: 40px;
  background: #eee;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.3s;
  -webkit-transition: 0.3s;
}

.switch.off {
  border-color: #6ab0f5;
  background: #6ab0f5;
  transition: 0.3s;
  -webkit-transition: 0.3s;
}

.switch.off em {
  box-shadow: 0 1px 2px #747474;
  left: 29px;
  transition: 0.3s;
  -webkit-transition: 0.3s;
}

.switch.off .before {
  -webkit-transform: scale(0, 0);
          transform: scale(0, 0);
  transition: 0.25s;
  -webkit-transition: 0.25s;
}

.dropdown_menu .switch, .dropdown_menu .switch.off {
  background: #2baa3f;
}

.dropdown_menu .switch.off {
  border-color: #2baa3f;
}

.prompt {
  min-width: 140px;
  border-radius: 5px;
  line-height: 50px;
  color: #fff;
  position: absolute;
  right: 10px;
  bottom: 50px;
  padding: 0 20px;
}

.waring {
  background: #da7373;
  box-shadow: 0 1px 24px rgba(123, 50, 50, .4);
}

.success {
  background: #4eb464;
  box-shadow: 0 1px 24px rgba(57, 144, 76, .4);
}

.load {
  line-height: 30px;
  bottom: 0;
  right: 0;
  background: #6ab0f5;
  border-radius: 5px 0 0 0;
}

/*适配*/
.container_large .form_column {
