}

/*控制dom位移的class*/
.move_main_top {
  -webkit-transform: translate(0, -100%);
          transform: translate(0, -100%);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

/*向上移动*/
.left_bar_hide {
  -webkit-transform: translate(-220px, 0);
          transform: translate(-220px, 0);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

/*隐藏左侧菜单*/
.right_main_hide {
  -webkit-transform: translate(0, 100%);
          transform: translate(0, 100%);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

