

























/*主体*/
.layout {
  width: 100%;
  height: 100%;
  position: relative;
  background: -webkit-gradient(linear, 0 0, 0 100%, from(#667c99), to(#b7c1cf));
}

.container {
  width: 100%;
  height: 100%;
  background: #f6f9fc;
  position: relative;
  overflow: hidden;
}

.container_large {
  width: 1366px;
  height: 80%;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -21% 0 0 -683px;
  box-shadow: 0 0 100px rgba(0, 0, 0, .4);
}

.create_layout {
  background: #e4edfb;
}

/*login*/
.login_header {
  width: 100%;
  height: 50px;
  line-height: 50px;
  background: #2c2b29;
  position: fixed;
  left: 0;
  top: 0;
}

.login_header .btn {
  height: 30px;
  line-height: 30px;
  padding: 0 20px;
  border-radius: 3px;
}

.login_box, .choose_net {
  width: 350px;
  height: 260px;
  padding: 20px;
  z-index: 100;
  background: #fff;
  box-shadow: 0 0 90px rgba(131, 148, 161, 0.5);
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -150px 0 0 -175px;
}

.login_in {
  border-radius: 3px;
  padding: 0 40px;
}

.login_in .has-general {
  width: 100%;
  height: 50px;
  line-height: 50px;
  border: 1px solid #c9d0df;
  border-radius: 5px;
}

.login_in .has-general input {
  border: 0;
  border-radius: 5px;
  /*box-shadow: none;*/
  text-indent: 20px;
  color: #a3a8b8;
}

.login_in .has-error input {
  background: none;
}

.login_box input[type=checkbox] {
  vertical-align: 0;
}

.login_box .bottom_btns {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
}

.login_box .bottom_btns a {
  width: 50%;
  height: 50px;
  line-height: 50px;
  background: #f7f8fb;
  color: #a6b3c0;
  border-radius: 0 0 0 5px;
}

.login_box .bottom_btns a:last-child {
  background: #6aaff4;
  color: #fff;
  border-radius: 0 0 5px 0;
}



